<template>
    <div class="box-container">
        <table>
            <tbody>
            <tr v-bind:key="key" v-for="(value, key) in data" >
                <td>
                    {{key}}
                </td>
                <td>
                    {{value}}
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        name: "PersonalPage",
        data: function () {
            return {
                data: null,
            }
        },
        mounted() {
            //Set path
            axios.get(`${this.$store.state.serverAddress}/rest/player/`,{
                headers: this.$store.getters.getHeader
            }).then((response) =>{
                //Mutate data
                this.data = Object.assign(response.data, response.data.clientdata, {clientdata: null});
            })
        }
    }
</script>

<style scoped  lang="scss">

    .box-container{
        width: 100%;
    }

    table {
        border: 2px solid #b94242;
        border-radius: 3px;
        background-color: #fff;
        width: 100%;
        margin-top: 20px;
    }

    th {
        background-color: #b94242;
        color: rgba(255, 255, 255, 0.66);
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }

    td {
        background-color: #f9f9f9;
        text-align: left;
    }

    th,
    td {
        padding: 5px 10px;
    }
</style>